import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Slider from '../components/Slider/Slider'
import "./index.css"

export default function GuestPage() {

  const [stopAnimation, setStopAnimation] = useState(false)
  const navigate = useNavigate()

  const mouseLeft = () => {
    setTimeout (() => { 
      setStopAnimation(false)
    }, 1000)
  }

  return (
    <div className='container'>
      <h1 className='title'> </h1>
      <div className="main__wrapper">
        <div className="main__slider"  onMouseEnter={() => setStopAnimation(true)} onMouseLeave={mouseLeft}>
            <Slider stopAnimation={stopAnimation}>
            </Slider>
        </div>
        <div className='main__text'>
          <h4>     .</h4>
          <p>  - !</p>
          <p>  ,   <span className="guest__link" onClick={() => navigate('/fo_homework4_post4/registration')}></span>   <span className="guest__link" onClick={() => navigate('/fo_homework4_post4/login')}></span></p>
        </div>
      </div>
    </div>
  )
}
